import React, { Fragment, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { getSkillQnA } from "../../reducers/skillReducer";
import AnimatedElement from "../../shared-components/AnimatedElement";
import SkillQnASection from "./SkillQnASection";
import { setLoading } from "../../reducers/commonReducer";

const SkillsQnA = (props) => {
  const skillId = props.skillId;
  const [skillQnAList, setSkillQnAList] = useState(null);
  const dispatch = useDispatch();

  const getSkillQnAList = async (skillId) => {
    dispatch(setLoading(true));
    const data = await dispatch(getSkillQnA({ skillId }));
    if (!data.payload.error) {
      await setSkillQnAList(data.payload);
    } else {
      await setSkillQnAList(null);
    }
    dispatch(setLoading(false));
  };

  useEffect(() => {
    getSkillQnAList(skillId);
  }, []);

  return (
    <div className="container-fluid p-0 profile">
      <div className="px-md-3">
        {skillQnAList &&
          skillQnAList.map((item, i) => (
            <Fragment key={item._id}>
              <AnimatedElement>
                <SkillQnASection item={item} index={i + 1} />
              </AnimatedElement>
            </Fragment>
          ))}
      </div>
    </div>
  );
};

export default SkillsQnA;
