import React, { Fragment, useEffect, useRef, useState } from "react";
import {
  Container,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
} from "reactstrap";
import { getUserSkillsData } from "../../reducers/skillReducer";
import { useDispatch, useSelector } from "react-redux";

import SkillViewDesc from "../../shared-components/skillView/skillViewDesc";
import FileComponent from "../../shared-components/FileComponent";

const Skill = (props) => {
  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [skillData, setSkillData] = useState(null);
  const [activeTab, setActiveTab] = useState(null);
  const elementRef = useRef(null);

  const scrollToElement = () => {
    if (elementRef.current) {
      const elementPosition = elementRef.current.offsetTop - 50;
      window.scrollTo({
        top: elementPosition,
        behavior: "smooth",
      });
    }
  };

  const toggleTab = (tab) => {
    if (activeTab !== tab) {
      setActiveTab(tab);
      scrollToElement();
    }
  };

  const getUserSkill = async () => {
    const data = await dispatch(
      getUserSkillsData({ token: userdetails.token })
    );
    if (!data.payload.error) {
      await setSkillData(data.payload);
      //await setActiveTab(data.payload[0]._id);
    } else {
      await setSkillData(null);
    }
  };

  useEffect(() => {
    setActiveTab(props.activeId ? props.activeId : null);
    getUserSkill();
  }, [userdetails, props.activeId]);

  return (
    <>
      <Container className="blogTech">
        <h3 className="t-color f-size-36 py-3 my-4 text-center">
          Capabilities and Expertise
        </h3>
        <div className="blogTech-container">
          {skillData && (
            <div className="d-flex">
              <div className="pt-4">
                <Nav pills vertical>
                  {skillData.map(
                    (item, i) =>
                      item.active && (
                        <Fragment key={`pill${item._id}`}>
                          <NavItem>
                            <NavLink
                              className={`text-nowrap p-1 ${
                                activeTab === item._id ? "active" : ""
                              }`}
                              onClick={() => toggleTab(item._id)}
                            >
                              <span className="d-none d-md-block">
                                {item.skillName}
                              </span>
                              <span className="avatar d-block d-md-none">
                                {item.skillImage ? (
                                  <FileComponent
                                    fileSrc={item.skillImage}
                                    fileClass=""
                                    alt={item.skillName}
                                  />
                                ) : (
                                  ""
                                )}
                              </span>
                            </NavLink>
                          </NavItem>
                        </Fragment>
                      )
                  )}
                </Nav>
              </div>
              <div
                className="flex-grow-1 p-2 p-md-4 pt-4 t-back2"
                ref={elementRef}
              >
                <TabContent activeTab={activeTab}>
                  {skillData.map(
                    (item, i) =>
                      item.active && (
                        <TabPane tabId={item._id} key={`tab${item._id}`}>
                          <SkillViewDesc item={item} />
                        </TabPane>
                      )
                  )}
                </TabContent>
              </div>
            </div>
          )}
        </div>
      </Container>
    </>
  );
};

export default Skill;
