import React from "react";
import Base from "../Base";

import Skill from "../components/SkillsView/Skills";
import { useParams } from "react-router-dom";

const Skills = () => {
  const { id } = useParams();
  return (
    <>
      <Base>
        <div className="container-fluid px-0">
          <Skill activeId={id} />
        </div>
      </Base>
    </>
  );
};

export default Skills;
