import React, { useState } from "react";
import parse from "html-react-parser";
import SkillsQnA from "./SkillsQnA";

const SkillViewDesc = (props) => {
  const [viewQnA, setViewQnA] = useState(false);

  return (
    <>
      <div className="d-flex justify-content-between">
        <h3 className="f-size-20 t-color">{props.item.skillName}</h3>
        <span
          className="f-bold f-size-15 p-color text-nowrap cursor-pointer"
          onClick={(e) => {
            setViewQnA(!viewQnA);
          }}
        >
          {!viewQnA ? "Show Q & A" : "Hide Q & A"}
        </span>
      </div>
      {!viewQnA ? (
        <>
          <h4 className="f-size-15 f-bold p-color">
            {props.item.skillHeading}--{props.item._Id}
          </h4>
          <div className="f-size-15 t-color">
            {parse(`${props.item.skillDesc}`)}
          </div>
        </>
      ) : (
        <>
          <h1>{props.item._Id}</h1>
          <SkillsQnA skillId={props.item._id} />
        </>
      )}
    </>
  );
};

export default SkillViewDesc;
