import { faEye, faEyeSlash } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useRef, useState } from "react";
import parse from "html-react-parser";

const SkillQnASection = (props) => {
  const [showAnswer, setShowAnswer] = useState(false);
  const divRef = useRef(null);
  const focusDiv = () => {
    if (divRef.current) {
      divRef.current.focus();
    }
  };
  return (
    <div className="border-line-light" key={props.item._id}>
      <h3 className="f-size-15 position-relative" tabIndex={0} ref={divRef}>
        <span
          className="p-color cursor-pointer"
          onClick={(e) => setShowAnswer(!showAnswer)}
        >
          <span className="pe-3">{props.index}.</span> {props.item.question}
        </span>
      </h3>
      {showAnswer ? (
        <div className="f-size-15 t-color ql-content">
          {parse(`${props.item.answer}`)}
          <div
            className="p-color f-size-15 f-bold cursor-pointer d-inline ps-3"
            onClick={(e) => {
              setShowAnswer(false);
              focusDiv();
            }}
          >
            <FontAwesomeIcon
              icon={faEyeSlash}
              className="mt-1"
              style={{ padding: "0px 7px" }}
            />
            Hide Answer
          </div>
        </div>
      ) : (
        <div
          className="p-color f-size-15 f-bold cursor-pointer d-inline ps-3"
          onClick={(e) => setShowAnswer(true)}
        >
          <FontAwesomeIcon
            icon={faEye}
            className="mt-1"
            style={{ padding: "0px 7px" }}
          />
          Show Answer
        </div>
      )}
    </div>
  );
};

export default SkillQnASection;
